import HButton from '../../../components/HButton.vue'
import { setInfo } from '../../../api/index.js'
import { uploadHeadPic } from '../../../api/config.js'
import { URL } from '../../../common/index.js'

export default {
	components: {
		HButton
	},
	data(){
		return{
			userInfo:{},
			URL,
			showPop:false,
			nickname:undefined,
			sex:undefined,
			sexRang:['女','男']
		}
	},
	onLoad() {
		this.userInfo = uni.getStorageSync('userInfo')
	},
	methods:{
		updataNickname(){
			return
			this.showPop = true
			this.nickname = this.userInfo.nickname
		},
		inputName(e){
			this.nickname = e.target.value
		},
		clear(){
			this.nickname = undefined
		},
		cancel(){
			this.showPop = false
		},
		success(){
			if(!this.nickname){
				uni.showToast({
					title:"昵称不能为空",
					icon:"none"
				})
				return
			}
			
		},
		updataPhoto(){

				return

			let _this = this
			uni.chooseImage({
			    count: 1, //默认9
			    sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: ['album'], //从相册选择
			    success: function (res) {
			        // console.log(JSON.stringify(res.tempFilePaths));
					let data = {
						userId : uni.getStorageSync('userId'),
						path:res.tempFilePaths[0]
					}
					uploadHeadPic(data).then(res=>{
						if(res.code==1){
							console.log(res);
							_this.userInfo.headPic = res.data
							uni.setStorageSync('userInfo',_this.userInfo)
						}
					})
			    }
			});
		},
		getPhoneNum(){

			uni.navigateTo({
				url:"/pages/Authorize/wx/index?step=2"
			})







		},
		bindSexChange(e){
			this.userInfo.sex = e.detail.value
		},
		bindBirthdayChange(e){
			this.userInfo.birthday = e.detail.value
		},
		submit(){
			let userId = uni.getStorageSync('userId');
			let data = {
			  id: userId,
			  sex: this.userInfo.sex,
			  birthday: this.userInfo.birthday
			}
			setInfo(data).then(res=>{
				if(res.code==1){
					uni.setStorageSync('userInfo',this.userInfo)
					uni.showToast({
						title:"设置成功",
					})
					setTimeout(()=>{
						uni.switchTab({
							url:"/pages/Tabbar/mine/index"
						})
					},1000)
				}else{
					uni.showToast({
						title:"设置失败，请稍后再试",
						icon:"error"
					})
				}
			})
		}
	}
}
